@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body {
  background: #5CDB95;
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
}


.search-box{
  position: absolute;
  top: 28%;
  left: 49%;
  transform: translate(-50%,-50%);
  background: #05386B;
  height: 40px;
  border-radius: 40px;
  padding: 10px;
}

.search-box:hover > .search-txt{
  width: 240px;
  padding: 0 6px;
}

.search-txt{
  border: none;
  background: none;
  outline: none;
  float: left;
  padding: 0;
  color: #EDF5E1;
  font-size: 30px;
  transition: 0.5s;
  line-height: 40px;
  width: 0px;
  font-family: 'Roboto', sans-serif;
}

::-webkit-input-placeholder{
  color: #EDF5E1;
}

:-ms-input-placeholder{
  color: #EDF5E1;
}

::-ms-input-placeholder{
  color: #EDF5E1;
}

::placeholder{
  color: #EDF5E1;
}


.imgSearch {
  font-size: 40px;
  color: #05386B;
  left: 40%;
  position: absolute;
  top: 12%;
}

.image__list{
  display: inline-block;
  grid-gap: 0 5px;
  grid-auto-rows: 10px;
  padding-top: 170px;
  padding-left: 100px;
}

.image__list img {
  border: 3px solid #05386B;
  transition: ease 0.3s;
  display: inline-block;
  margin: 10px;
  width: 220px;
  height: 220px;
}

.image__list img:hover{
  position: relative;
  z-index: 2;
  transform: scale(1.10);
  transition: .3s;
  box-shadow: 0 0 15px 5px black;
}

.displayImage {
  margin-top: 200px;
  text-align: center;
}

.displayImage img{
  max-width: 500px;
  max-height: 350px;
  border: 3px solid #05386B;
  transition: ease 0.3s;
}

.displayImage h2{
  color: #05386B;
  font-size: 30px;
}

.displayImage p{
  color: #05386B;
  font-size: 20px;
}

.displayImage button{
  border-radius: 50px;
  background-color: #379683;
  color: #EDF5E1;
  font-family: 'Roboto', sans-serif;
  border: none;
  font-size: 23px;
  padding: 9px 25px;
  margin-left: 20px;
  transition: all 0.3s ease 0s;
  cursor: pointer;
}

.displayImage button:hover{
  background-color: #05386B;
}

.showImg{
  display: inline-block;
  padding-top: 180px;
  padding-left: 50px;
}

.showImg img {
  border: 3px solid #05386B;
  transition: ease 0.3s;
  display: inline-block;
  margin: 10px;
  width: 220px;
  height: 220px;
}

.showImg img:hover{
  position: relative;
  z-index: 2;
  transform: scale(1.10);
  transition: .3s;
  box-shadow: 0 0 15px 5px black;
}



.nav__links{
  font-weight: 500;
  font-size: 23px;
  color: #EDF5E1;
  text-decoration: none;
  list-style: none;
  cursor: pointer;
  margin-left: -70px;
}


.nav__links li {
  display: inline-block;
  padding: 9px 25px;
  text-decoration: none;
  border: none;
  border-radius: 50px;
  background-color: #379683;
  transition: all 0.3s ease 0s;
  margin-left: 20px;
}
.nav__links li:hover{
  background-color: #8EE4AF;
}

.nav__links li a{
  transition: all 0.3s ease 0s;
}

.nav__links li a:hover{
  text-decoration: none;
}

a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
  color: #EDF5E1;
}

header{
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 10px 10%;
  background-color:  #05386B;
  margin: 0;
}

#welcome{
  cursor: default;
  padding: 0;
  border-radius: 0;
  background-color: #05386B;
  position: absolute;
  left: 50px;
  top: 30px;
  font-size: 40px;
}

.imgResult {
  display: inline-block;
}

