.image__list{
  display: inline-block;
  grid-gap: 0 5px;
  grid-auto-rows: 10px;
  padding-top: 170px;
  padding-left: 100px;
}

.image__list img {
  border: 3px solid #05386B;
  transition: ease 0.3s;
  display: inline-block;
  margin: 10px;
  width: 220px;
  height: 220px;
}

.image__list img:hover{
  position: relative;
  z-index: 2;
  transform: scale(1.10);
  transition: .3s;
  box-shadow: 0 0 15px 5px black;
}
